






























import Vue from "vue";
import { mapMutations } from "vuex";
export default Vue.extend({
  name: "InputComponent",
  data() {
    return {
      hours: null,
      minutes: null,
    };
  },
  methods: {
    ...mapMutations(["addSession"]),
    initAddMutation() {
      const hours = Number(this.hours);
      const minutes = Number(this.minutes);
      this.addSession({ hours, minutes });
      this.hours = null;
      this.minutes = null;
    },
  },
});
